.container {
  background-color: white;
  box-shadow: 0px 1px 5px 1px black;
  width: 48%;
  display: flex;
  border-radius: 25px;
}
.container img {
  width: 45%;
  border-radius: 25px;
}
.container .rightPart {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 55%;
  row-gap: 20px;
}
.container .rightPart h5 {
  font-weight: 400;
  font-size: 19px;
  letter-spacing: 1px;
  color: #333333;
  text-shadow: 0px 1px 1px #757575;
}
.container .rightPart button {
  padding: 5px 25px;
  border-radius: 10px;
  background-color: #014D55;
  color: white;
  font-size: 14px;
  border: 1px solid #003338;
  letter-spacing: 1px;
  text-shadow: 0px 1px 1px gray;
  transition: all 400ms ease;
}
.container .rightPart button:hover {
  cursor: pointer;
  background-color: #013c42;
}

