.section {
  background-color: #F5F8F9;
  box-sizing: border-box;
  padding: 50px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}
.section .cardOfInfo {
  width: 60%;
  margin-top: 10px;
  background-color: #efefef;
  padding: 40px 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0px 0px 15px 1px inset rgba(224, 220, 220, 0.74), 0px 0px 10px 1px rgba(175, 175, 175, 0.87);
}
.section .cardOfInfo .mainInfo {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.section .cardOfInfo .mainInfo img {
  border-radius: 100%;
  width: 200px;
  height: 200px;
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.44);
  border: 1px solid #2c2c2c;
}
.section .cardOfInfo .mainInfo h4 {
  margin-bottom: 10px;
  letter-spacing: 1px;
  font-size: 21px;
  text-shadow: 0px 1px 0px #020202;
  color: #525252;
  text-transform: uppercase;
}
.section .cardOfInfo .mainInfo p {
  width: 450px;
  font-size: 19px;
  line-height: 1.4;
  text-shadow: 0px 0px 1px black;
  color: #575757;
  margin-bottom: 10px;
  text-align: justify;
}
.section .cardOfInfo .mainInfo > span {
  font-size: 20px;
  line-height: 1.4;
  text-shadow: 0px 0px 1px black;
  color: #575757;
}
.section .cardOfInfo button {
  margin-top: 20px;
}

